/*****************************/
/** CounterWidget **/
/*****************************/
.Card {
  display: grid;
  grid-template-rows: auto 150px;
  background-blend-mode: multiply;
}

.Card .Icon.Icon-Main {
  display: grid;
  place-content: center;
  font-size: 8rem;
}

.Card__Actions {
  padding: 0 20px;
  font-size: 1.8rem;
}

.Card__Actions .Icon {
  opacity: .7
}

.Card__Value {
  font-size: 1.8rem
}

.Card--f {
  background-color: rgba(41, 0, 14, 0.50);
}

.Card--d {
  background-color: rgba(0,0,0, .50)
}

.Card--m {
  background-color: rgba(0, 14, 27, 0.50)
} 