* {
  box-sizing: border-box;
  border: 0;
}

*:focus {
  outline: none
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-image: url(/static/media/bg-min.5ee12aae.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  color: white;
  font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    display: grid;
    width: 100vw;
    height: 100vh;

}

@media (orientation: landscape) {
    .App {
        grid-template-columns: calc( (100vw - 150px) / 2 ) 150px calc( (100vw - 150px) / 2 );
    }

}

@media (orientation: portrait) {
    .App {
        grid-template-rows: 1fr 100px 1fr;
    }
}



/*****************************/
/** CARD **/
/*****************************/
.Card--d {
    display: flex!important;
    align-items: center;
    justify-content: center
}

@media (orientation: landscape) {
    .Card--d {
        flex-direction: column;
    }
    
    .Card--d .Card__Value {
        margin: 20px 0
    }
}

@media (orientation: portrait) {
    .Card--d {
        flex-direction: row-reverse;
    }
    
    .Card--d .Card__Value {
        margin: 0 20px
    }
}


/*****************************/
/** Menu **/
/*****************************/
.MenuBtn {
    position: fixed;
    left: 20px;
    top: 20px
}

.Menu {
    position: fixed;
    top: 0;
    left: -100%;
    height: 100%;
    width: 30%;
    background-color: rgb(13, 14, 13);
    z-index: 10;
    padding: 20px;
    transition: all .3s ease-in-out;
    box-shadow: 1px 0px 3px #000000a6, 0 0 30px #000000;
    font-size: 1.2rem
}

@media (orientation: landscape) {
    .Menu {
        width: 30%;
    }
}

@media (orientation: portrait) {
    .Menu {
        width: 90%;
    }
}



.Menu[data-open="true"] {
    left: 0;
}

.Menu__Close {
    position: absolute;
    right: 10px;
    top: 10px;
    width: 20px;
    transform: rotate(-45deg);
    font-size: 2rem;
    height: 20px;
    display: grid;
    place-content: center;
}

.Menu__Close::before {
    content: "+";
}

.Menu ul {
    padding: 0;
    list-style: none;
    font-weight: 400;
}

.Menu ul li{
    padding: 10px 0;
    cursor: pointer;
    border-bottom: 1px solid rgb(51, 51, 51);
}
/*****************************/
/** BUTTON **/
/*****************************/

.Btns-group {
    display: flex;
    align-items: center;
}

.Btns-group .Btn {
    margin-right: 20px;
}

.Btns-group .Btn:last-child {
    margin-right: 0
}

.Btn {
    background-color: transparent;
    border: 0;
    transition: background-color .2s ease-in-out
}



/*****************************/
/** CounterWidget **/
/*****************************/
.Card {
  display: grid;
  grid-template-rows: auto 150px;
  background-blend-mode: multiply;
}

.Card .Icon.Icon-Main {
  display: grid;
  place-content: center;
  font-size: 8rem;
}

.Card__Actions {
  padding: 0 20px;
  font-size: 1.8rem;
}

.Card__Actions .Icon {
  opacity: .7
}

.Card__Value {
  font-size: 1.8rem
}

.Card--f {
  background-color: rgba(41, 0, 14, 0.50);
}

.Card--d {
  background-color: rgba(0,0,0, .50)
}

.Card--m {
  background-color: rgba(0, 14, 27, 0.50)
} 
/*****************************/
/** SEPARATOR **/
/*****************************/
.Separator {
  margin-top: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid white;
  opacity: .7
}

/*****************************/
/** ROW **/
/*****************************/

.Row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.Btn--Icon:hover {
  background-color: rgba(0,0,0, 0.5)
}

.Btn--Icon {
  display: grid;
  place-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 2rem;
}
